.profile {
    display: flex;
}

.profileright {
    flex: 9;
}

.profilecover {
    height: 320px;
    position: relative;
}

.profilecoverimg {
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.profileuserimg {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 150px;
    border: 3px solid white;
}

.profileinfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}

.profileinfoname {
    margin: 0;
    padding: 0;
    font-size: 24px;
}

.profileinfodesc {
    font-weight: 300;
}

.profilerightbottom {
    display: flex;
}